import { useState, useCallback, useRef } from 'react';

export const useSlashDetection = (
  canvasRef, 
  items, 
  gameState, 
  onUpdateScore, 
  onLoseLife, 
  onCreateParticles, 
  onCreateScreenFlash
) => {
  const [isSlashing, setIsSlashing] = useState(false);
  const [slashPath, setSlashPath] = useState([]);
  const lastMousePos = useRef({ x: 0, y: 0 });

  const getMousePos = useCallback((e) => {
    if (!canvasRef.current) return { x: 0, y: 0 };
    
    const rect = canvasRef.current.getBoundingClientRect();
    const scaleX = canvasRef.current.width / rect.width;
    const scaleY = canvasRef.current.height / rect.height;
    
    return {
      x: (e.clientX - rect.left) * scaleX,
      y: (e.clientY - rect.top) * scaleY
    };
  }, [canvasRef]);

  const getDistanceToLine = useCallback((lineStart, lineEnd, point) => {
    const dx = lineEnd.x - lineStart.x;
    const dy = lineEnd.y - lineStart.y;
    const length = Math.sqrt(dx * dx + dy * dy);
    
    if (length === 0) {
      const dx2 = point.x - lineStart.x;
      const dy2 = point.y - lineStart.y;
      return Math.sqrt(dx2 * dx2 + dy2 * dy2);
    }
    
    const t = Math.max(0, Math.min(1, ((point.x - lineStart.x) * dx + (point.y - lineStart.y) * dy) / (length * length)));
    const projectionX = lineStart.x + t * dx;
    const projectionY = lineStart.y + t * dy;
    
    const distanceX = point.x - projectionX;
    const distanceY = point.y - projectionY;
    
    return Math.sqrt(distanceX * distanceX + distanceY * distanceY);
  }, []);

  const checkSlashCollisions = useCallback((start, end) => {
    items.forEach((item) => {
      if (item.slashed) return;
      
      const distance = getDistanceToLine(start, end, { x: item.x, y: item.y });
      
      if (distance < item.radius) {
        item.slashed = true;
        
        if (item.type.isGood) {
          // Fruit - award points
          onUpdateScore(item.type.points);
          onCreateParticles(item.x, item.y, '#00ff88', 15);
          
          // Create fruit slice effect
          const canvas = canvasRef.current;
          if (canvas) {
            const rect = canvas.getBoundingClientRect();
            const effect = document.createElement('div');
            effect.className = 'fruit-slice-effect';
            effect.style.position = 'fixed';
            effect.style.left = (rect.left + item.x - 30) + 'px';
            effect.style.top = (rect.top + item.y - 30) + 'px';
            effect.style.background = `radial-gradient(circle, ${item.type.color}88, transparent)`;
            effect.style.pointerEvents = 'none';
            effect.style.zIndex = '999';
            
            document.body.appendChild(effect);
            
            setTimeout(() => {
              if (document.body.contains(effect)) {
                document.body.removeChild(effect);
              }
            }, 500);
          }
        } else {
          // Bomb - lose life
          onLoseLife();
          onCreateParticles(item.x, item.y, '#ff4444', 20);
          onCreateScreenFlash();
          
          // Create bomb explosion effect
          const canvas = canvasRef.current;
          if (canvas) {
            const rect = canvas.getBoundingClientRect();
            const effect = document.createElement('div');
            effect.className = 'bomb-explosion-effect';
            effect.style.position = 'fixed';
            effect.style.left = (rect.left + item.x - 40) + 'px';
            effect.style.top = (rect.top + item.y - 40) + 'px';
            effect.style.background = 'radial-gradient(circle, #ff4444, #ff8888, transparent)';
            effect.style.width = '80px';
            effect.style.height = '80px';
            effect.style.pointerEvents = 'none';
            effect.style.zIndex = '999';
            effect.style.borderRadius = '50%';
            effect.style.animation = 'explode 0.6s ease-out forwards';
            
            document.body.appendChild(effect);
            
            setTimeout(() => {
              if (document.body.contains(effect)) {
                document.body.removeChild(effect);
              }
            }, 600);
          }
        }
      }
    });
  }, [items, getDistanceToLine, onUpdateScore, onLoseLife, onCreateParticles, onCreateScreenFlash, canvasRef]);

  const startSlash = useCallback((e) => {
    if (gameState.screen !== 'game' || !gameState.isGameRunning || gameState.isPaused) return;
    
    setIsSlashing(true);
    const pos = getMousePos(e);
    lastMousePos.current = pos;
    setSlashPath([pos]);
  }, [gameState.screen, gameState.isGameRunning, gameState.isPaused, getMousePos]);

  const updateSlash = useCallback((e) => {
    if (!isSlashing || gameState.screen !== 'game' || !gameState.isGameRunning || gameState.isPaused) return;
    
    const currentPos = getMousePos(e);
    
    setSlashPath(prev => [...prev, currentPos]);
    
    checkSlashCollisions(lastMousePos.current, currentPos);
    lastMousePos.current = currentPos;
  }, [isSlashing, gameState.screen, gameState.isGameRunning, gameState.isPaused, getMousePos, checkSlashCollisions]);

  const endSlash = useCallback(() => {
    if (!isSlashing) return;
    
    setIsSlashing(false);
    
    setTimeout(() => {
      setSlashPath([]);
    }, 300);
  }, [isSlashing]);

  return {
    startSlash,
    updateSlash,
    endSlash,
    slashPath
  };
};