import React, { useRef, useEffect, useCallback } from 'react';
import { useGameLoop } from '../hooks/useGameLoop';
import { useSlashDetection } from '../hooks/useSlashDetection';

const GameScreen = ({ 
  gameState, 
  onEndGame, 
  onUpdateScore, 
  onLoseLife, 
  onTogglePause,
  onCreateParticles,
  onCreateScreenFlash,
  updateParticles
}) => {
  const canvasRef = useRef(null);
  const ctxRef = useRef(null);

  const { 
    items, 
    slashTrail, 
    particles,
    spawnItem,
    updateGame,
    render
  } = useGameLoop(canvasRef, gameState, onEndGame, updateParticles);

  const {
    startSlash,
    updateSlash,
    endSlash
  } = useSlashDetection(
    canvasRef,
    items,
    gameState,
    onUpdateScore,
    onLoseLife,
    onCreateParticles,
    onCreateScreenFlash
  );

  useEffect(() => {
    const canvas = canvasRef.current;
    if (!canvas) return;

    const ctx = canvas.getContext('2d');
    ctxRef.current = ctx;

    // Resize canvas
    const resizeCanvas = () => {
      if (window.innerWidth <= 768) {
        canvas.width = window.innerWidth;
        canvas.height = window.innerHeight - 80;
      } else {
        canvas.width = 800;
        canvas.height = 600;
      }
    };

    resizeCanvas();
    window.addEventListener('resize', resizeCanvas);

    return () => {
      window.removeEventListener('resize', resizeCanvas);
    };
  }, []);

  useEffect(() => {
    if (!gameState.isGameRunning || gameState.isPaused) return;

    const gameLoop = setInterval(updateGame, 16);
    const itemSpawner = setInterval(spawnItem, 1200);

    return () => {
      clearInterval(gameLoop);
      clearInterval(itemSpawner);
    };
  }, [gameState.isGameRunning, gameState.isPaused, updateGame, spawnItem]);

  useEffect(() => {
    render(ctxRef.current, items, slashTrail, particles);
  }, [items, slashTrail, particles, render]);

  const handleMouseDown = useCallback((e) => {
    startSlash(e);
  }, [startSlash]);

  const handleMouseMove = useCallback((e) => {
    updateSlash(e);
  }, [updateSlash]);

  const handleMouseUp = useCallback(() => {
    endSlash();
  }, [endSlash]);

  const handleTouchStart = useCallback((e) => {
    e.preventDefault();
    startSlash(e.touches[0]);
  }, [startSlash]);

  const handleTouchMove = useCallback((e) => {
    e.preventDefault();
    updateSlash(e.touches[0]);
  }, [updateSlash]);

  const handleTouchEnd = useCallback((e) => {
    e.preventDefault();
    endSlash();
  }, [endSlash]);

  return (
    <div className="screen game-screen">
      <div className="game-header">
        <div className="score-container">
          <div className="score-label">Score</div>
          <div className="score-value">{gameState.score}</div>
        </div>
        
        <div className="lives-container">
          <div className="lives-label">Lives</div>
          <div className="hearts">
            {[1, 2, 3].map(i => (
              <span 
                key={i}
                className={`heart ${i > gameState.lives ? 'lost' : ''}`}
              >
                ♥
              </span>
            ))}
          </div>
        </div>
      </div>
      
      <div className="game-area">
        <canvas 
          ref={canvasRef}
          className="game-canvas"
          onMouseDown={handleMouseDown}
          onMouseMove={handleMouseMove}
          onMouseUp={handleMouseUp}
          onTouchStart={handleTouchStart}
          onTouchMove={handleTouchMove}
          onTouchEnd={handleTouchEnd}
        />
      </div>
      
      <button 
        className="btn btn--outline pause-btn" 
        type="button"
        onClick={onTogglePause}
      >
        <span>{gameState.isPaused ? '▶️' : '⏸️'}</span>
      </button>
    </div>
  );
};

export default GameScreen;